.catalog__title {
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
}

.catalog__list {
  @include list;

  @media (min-width: $tablet-width) {

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 68px 60px;
  }

  @media (min-width: $desctop-width) {
    grid-template-columns: repeat(4, 1fr);
    gap: 62px 80px;
  }
}


.catalog__item {
  min-width: 280px;

  @media (min-width: $tablet-width) {
    width: 324px;
  }

  @media (min-width: $desctop-width) {
    width: 245px;
  }
}
